<template>
  <div id="app" v-loading="loading">
    <div class="app-header">
      <el-menu
        :default-active="activeMenu"
        mode="horizontal"
        :router="true">

        <li style="width: 70px;float: left" >
          <img src="../../static/logo-yuedu.png" style="width: 60px;height: 60px"/>
        </li>

        <el-menu-item  v-for="(menu, index) in menuAuth" :key="index" :index="index" v-if="menu.show">{{menu.desc}}
        </el-menu-item>

        <!--  -->
        <li style="float: right;height: 60px;line-height: 60px;color:#909399;font-size: 14px">
          <a class="account-num" v-if="!accountsError && !!user">
            {{user.name}} &nbsp; {{accounts[0]}} 
          </a>

          <span v-if="accountsError && !needSingUp">
            <span>{{accountsError}}</span>
          </span>

          &nbsp;
          <a v-if="needSingUp" @click="goSignUp" style="color:#409EFF">
             {{accounts[0]}} 
          </a>
          &nbsp;
        </li>

        <!--   -->
        <li style="float: right;width: 50px;"  v-if="!(!accountsError && !!user)">
          <img src="../../static/logo.png" style="width: 40px;height:40px; margin-top:10px;"/>
        </li>

        <!--  -->
        <li style="float: right;width:35px;height:35px;line-height: 75px"  v-if="!accountsError && !!user">
          <vue-letter-avatar :name='user.name' size='30' :rounded='true' />
        </li>
      </el-menu>
    </div>

    <!---->
    <div class="app-content">
      <router-view></router-view>
    </div>

    <div class="app-footer">
       <a href="https://nebulas.io" target="_blank"></a> &nbsp;
      |&nbsp;<a href="mailto:zhuku9126@yandex.com"></a>&nbsp;
      |&nbsp;<a href="https://incentive.nebulas.io/cn/signup.html?invite=Xr5bL" target="_blank">DAPP100NAS</a>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      loading: false,
      menuAuth: {
        index: {show: true, desc: ''},
        publish: {show: false, desc: ''},
        mine: {show: false, desc: ''},
        signUp: {show: false, desc: ''},
        help: {show: true, desc: ''},
        detail: {auth: true, desc: ''}
      }
    }
  },
  methods: {
    goHelp: function () {
      this.$router.push('/help')
    },
    goSignUp: function () {
      console.log('here')
      this.$router.push('/signUp')
    },
    // 
    loadBaseAcc: function () {
      var vueThis = this
      vueThis.$store.dispatch('initBaseAccount').then(() => {
        // 
        vueThis.isValidRouteAuth()
      }, function (error) {
        vueThis.showErr(error);
        vueThis.isValidRouteAuth()
      })
    },
    // 
    isValidRouteAuth: function (to) {
      let vueThis = this
      vueThis.loading = true;
      // vueThis.menuAuthshow
      let menuKey = to ? to.name : vueThis.$route.name;
      let isLogin = !vueThis.accountsError && !!vueThis.user;

      // 
      vueThis.menuAuth.publish.show = isLogin;
      vueThis.menuAuth.mine.show = isLogin;
      vueThis.menuAuth.signUp.show = vueThis.needSingUp;

      // 
      if (!vueThis.menuAuth[menuKey].show && !vueThis.menuAuth[menuKey].auth) {
        vueThis.$router.push('/help')
      }
      vueThis.loading = false;
    }
  },
  created () {
    this.loadBaseAcc()
  },
  computed: mapState([
    'activeMenu',
    'accounts',
    'needSingUp',
    'accountsError',
    'user'
  ]),
  watch: {
    user (curVal, oldVal) {
      if (!_.isEqual(curVal, oldVal)) {
        console.log('')
        this.isValidRouteAuth()
      }
    },
    $route (to) {
      console.log('to==', to)
      this.isValidRouteAuth(to)
    }
  }
}
</script>

<style scoped>
  #app{
    padding-left:40px;
    padding-right:40px;
  }

  #app .app-header{
    font-size: 16px;
  }

  #app .app-content{
    padding-top: 15px;
    min-height: 500px;
  }

  #app .app-footer {
    color: #606266;
    margin-top: 45px;
    padding: 35px;
    text-align: center;
    font-size: 14px;
    border-top: 1px solid #DCDFE6
  }
  #app .app-footer a{
    color: #606266;
  }
</style>



