<template>
  <div id="publish">

    <el-steps :active="step" simple>
      <el-step title="1." icon="el-icon-tickets"></el-step>
      <el-step title="2." icon="el-icon-edit"></el-step>
      <el-step title="3." icon="el-icon-success"></el-step>
    </el-steps>
    <br>

    <el-row v-if="bookInfo" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-collapse accordion v-model="collapseActive">
          <el-collapse-item name="firstC">

            <template slot="title">
               {{bookInfo.title}} &nbsp;[]
            </template>

            <el-row style="background-color: gainsboro">
              <el-col :span="24" style="padding: 5px">

                <img :src="bookInfo.cover" class="image" style="max-height: 210px;max-width: 210px;float: left;padding: 10px">

                <h3 style="text-align: left">
                  {{bookInfo.title}}
                </h3>

                <span class="post_meta_top">
                  {{bookInfo.author}}
                </span>

                <p class="post_content">
                  {{bookInfo.desc}}
                </p>
              </el-col>
            </el-row>

          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>


    <el-row v-show="step == 1" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="loadBook" :model="loadBook" :rules="loadBookRules" label-position="top">
          <el-form-item label=", : https://book.douban.com/subject/26871829/" prop="url">
            <el-input minlength="1" maxlength="150" v-model="loadBook.url" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="loadBookFun"></el-button>
            <el-button @click="resetForm('loadBook')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <el-row v-show="step == 2" v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">
          <el-form-item label="" prop="readTitle">
            <el-input minlength="1" maxlength="150" v-model="form.readTitle" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="comment">
            <el-input type="textarea" :autosize="{ minRows: 15, maxRows: 25}"
                      minlength="1" maxlength="1500"
                      v-model="form.comment" placeholder="">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitNode"></el-button>
            <el-button @click="resetForm('form')"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <el-row v-show="step == 3" v-loading="loading">
      <el-col :offset="2" :span="20">
        <h1></h1>
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">
          <el-form-item>
            <el-button type="primary" @click="again"></el-button>
            <el-button type="primary" @click="returnHome"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import axios from 'axios'

export default {
  data () {
    return {
      collapseActive: 'firstC',
      step: 1,
      loading: false,
      loadBook: {
        url: ''
      },
      loadBookRules: {
        url: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 350, message: ' 1  350 ', trigger: 'blur'}
        ]
      },
      bookInfo: null,
      form: {
        readTitle: '',
        comment: ''
      },
      formRules: {
        readTitle: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        comment: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 1500, message: ' 1  1500 ', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 
    again: function () {
      this.bookInfo = null;
      this.step = 1;
      this.loadBook.url = '';
      this.form.readTitle = '';
      this.form.comment = '';
    },
    // 
    returnHome: function () {
      this.$router.push({name: 'index'})
    },
    loadBookFun: function () {
      var vueThis = this
      vueThis.$refs['loadBook'].validate((valid) => {
        if (valid) {
          vueThis.loading = true
          axios.post('http://45.76.2.218:8061/', {url: vueThis.loadBook.url}).then(response => {
            if (response.data.title && response.data.cover) {
              vueThis.bookInfo = response.data;
              vueThis.step = 2;
              vueThis.loading = false
            } else {
              vueThis.loading = false
              vueThis.showErr('')
            }
          }).catch(function (error) {
            vueThis.loading = false
            vueThis.showErr('', error)
          })
        } else {
          vueThis.showErr('');
        }
      })
    },
    submitNode: function () {
      var vueThis = this
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var b = vueThis.bookInfo;
          var f = vueThis.form;
          let params = [b.url, b.cover, b.title, b.author, b.desc, f.readTitle, f.comment];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'createRead', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('');
            vueThis.step = 3;
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    resetForm: function (formName) {
      this.$refs[formName].resetFields();
    }
  },
  created () {
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #publish {
  }

  #publish .post_content {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #publish .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }
</style>



