<template>
  <div id="loginUser" v-loading="loading">
    <el-row>
      <el-col :offset="4" :span="16">
        <el-form ref="form" :model="form" label-width="80px" :rules="formRules" label-position="top" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="form.name" minlength="1" maxlength="50" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="addinfo">
            <el-input type="textarea"  minlength="1" maxlength="350"
                      v-model="form.addinfo" :autosize="{ minRows: 2, maxRows: 4}"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="avatar" v-if="false">
            <el-color-picker v-model="form.avatar" placeholder=""></el-color-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="signUp"></el-button>
            <el-button @click="resetForm()"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      loading: false,
      form: {
        name: '',
        avatar: '#409EFF',
        addinfo: ''
      },
      formRules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 50, message: ' 1  50 ', trigger: 'blur'}
        ],
        avatar: [
          {required: true, message: '', trigger: 'blur'}
        ],
        addinfo: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 350, message: ' 1  350 ', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    signUp: function () {
      var vueThis = this
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          let f = vueThis.form;
          let params = [f.name + '', f.avatar + '', f.addinfo + ''];
          vueThis.loading = true;
          vueThis.$store.dispatch('callNew', {funcName: 'createUser', params: params, goods: {name: ''}}).then((ret) => {
            vueThis.loading = false;
            console.log('', ret);
            vueThis.showSuccess(', 3');
            _.delay(function () {
              vueThis.$router.push('/index');
              _.delay(function () {
                location.reload(true);
              }, 300)
            }, 3000)
          }, (error) => {
            console.log('', error);
            vueThis.loading = false;
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    resetForm: function () {
      this.$refs['form'].resetFields();
    }
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #loginUser {
  }
</style>



